img.torrent-img {
  opacity: 0;
  transition: all 0.25s ease;
}

[data-rmiz-wrap="visible"],
[data-rmiz-wrap="hidden"] {
  width: 100%;
  max-width: 700px;
  max-height: 700px;
  opacity: 1;
  visibility: visible;
  transition: all 0.25s ease;
}

[data-rmiz-wrap="hidden"] {
  opacity: 0;
  visibility: hidden;
  transition: all 0.25s ease;
}

[data-rmiz-modal-content] {
  background-color: #171c23;
}

[data-rmiz-modal-content] .torrent-img {
  display: none;
}

img.enlarged-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center;
  opacity: 0;
  transition: all 0.2s ease;
}

.user-page .posts-holder .post .content img.enlarged-img {
  display: none;
}

@media only screen and (max-width: 500px) {
  .user-page .posts-holder .post .content img {
    border-radius: 0;
    object-fit: cover;
  }
}
